import React from 'react';
import './App.css';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      username:null
    };
  }

  render() {
    return (<div clasName="App">
      <header className="App-header">
        {this.state.username ? `Hello ${this.state.username}` : 'Hellow World'}
      </header>
    </div>);
  }

  componentDidMount(){
    fetch('http://nextwind77.cafe24app.com/users')
    .then(res=>res.json())
    .then(data=>this.setState({username:data.username}));
  }

}

export default App;
